@import '@/styles/public.scss';

.addTask-box {
  .content-box {
    height: 500px;
    overflow: auto;
    .list-box {
      padding: 0 40px;

      .list-item {
        display: flex;
        justify-content: space-between;
        height: 40px;
        line-height: 40px;

        .list-items {
          display: flex;
          flex: 1;

          .item-title,
          .item-content span {
            font-size: 12px;
          }

          .item-title {
            width: 100px;
            text-align: right;
            margin-right: 5px;
          }

          .item-content {
            span {
              color: #999999;
            }

            .view-button {
              margin-left: 10px;
            }
            /deep/ .el-button {
              color: #4b77be;
              border-color: #4b77be;
            }
            /deep/ .el-button:hover {
              background: #4b77be;
              color: #ffffff;
            }
            .enclosure-box {
              .enclosure-item {
                display: flex;

                .enclosure-title,
                .enclosure-download,
                .enclosure-preview {
                  padding: 0 10px;
                }
              }
            }
          }
        }
        .has-two-item {
          width: 46%;
        }
      }
      .workOrder-back-box {
        width: 100%;
        .back-box {
          display: flex;
          .back-title {
            width: 100px;
            text-align: right;
            line-height: 40px;
            font-size: 12px;
          }
          .back-content {
            flex: 1;
            width: 100%;
            .back-item {
              width: 46%;
              line-height: 20px;
              margin-top: 9px;
              color: #999999;
              font-size: 12px;

              .zhi {
                margin-left: 20px;
              }
            }
            .back-item:nth-child(odd) {
              float: left;
            }
            .back-item:nth-child(even) {
              float: right;
            }
          }
        }
      }
    }
  }
}
