


































































































.upload-box {
  .list-box {
    width: 90%;
    /deep/ .el-table {
      font-size: 12px;
    }
    /deep/ .el-table thead {
      font-weight: 400;
      color: #333333;
    }
    /deep/ .el-table th.is-leaf,
    /deep/ .el-table td {
      border-bottom: none;
    }
    /deep/ .el-table th, {
      padding: 2px 0;
      line-height: 23px;
    }
    /deep/ .el-table td {
      padding: 5px;
    }
  }
}
